<template>
    <div>
      <div id="app">
        <div class="container">
          <div class="panel panel-sm">
            <div class="panel-heading"> 
              <h4>CSV Import</h4>
            </div>
            <div class="panel-body">
              <div class="form-group">
                <label for="csv_file" class="control-label col-sm-3 text-right">CSV file to import</label>
                <div class="col-sm-9">
                  <input type="file" id="csv_file" name="csv_file" class="form-control" @change="loadCSV($event)">
                </div>
              </div>
              <div class="col-sm-offset-3 col-sm-9">
                <div class="checkbox-inline">
                  <label for="header_rows"><input type="checkbox" id="header_rows"> File contains header row?</label>
                </div>
              </div>
              
              <table v-if="parse_csv">
                <thead>
                  <tr>
                    <th v-for="key in parse_header" :key="key">
                      {{ key}}
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr :key="index" v-for="(item,index) in parse_csv">
                    <td>{{item.name_supplier}}</td>
                    <td>{{item.name_toko}}</td>
                    <td>{{item.tanggal}}</td>
                    <td>{{item.noDo}}</td>
                    <td>{{item.sku}}</td>
                    <td>{{item.deskripsi}}</td>
                    <td>{{item.qty}}</td>
                    <td>{{item.satuan}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          
        </div>
      </div>
    </div>
</template>

<script>
export default {
    data() {
      return{
        parse_header: [],
        parse_csv: [],
      }
    },

    methods: {
        csvJSON(csv){
            let lines = csv.split("\n")
            let result = [];
            // var headers = lines[0].split(",")
            this.parse_header = ['Supplier','Toko','Tanggal','No DO','SKU','Deskripsi','Qty','Satuan'] 
            
            let parse_row_one = lines[0].split(";")
            let name_supplier = parse_row_one[2]
            // console.log(name_supplier);
            let parse_row_two = lines[4].split(";")
            let name_toko = parse_row_two[1].replace(":", "");
            // console.log(name_toko);
            let parse_row_three = lines[6].split(";")
            let tanggal = parse_row_three[8]
            // console.log(tanggal);
            let parse_row_four = lines[6].split(";")
            let noDo = parse_row_four[10]
            // console.log(noDo);

            for(let i = 0 ; i < lines.length; i++)
            {
              
              if(lines[i].length > 0) {
                if(lines[i].charAt(0) == " ") {

                  let parse_row_five = lines[i].split(";")
   
                  let sku = parse_row_five[0]
                  // console.log(sku);
                  let deskripsi = parse_row_five[3]
                  // console.log(deskripsi);
                  let qty = parse_row_five[9]
                  // console.log(qty);
                  let satuan = parse_row_five[10]

                  let obj = {
                    name_supplier : name_supplier,
                    name_toko : name_toko,
                    tanggal : tanggal,
                    noDo : noDo,
                    sku : sku,
                    deskripsi : deskripsi,
                    qty : qty,
                    satuan : satuan
                  }
                  result.push(obj)
                }
              }
            }
            
            return result;
            
        },

        loadCSV(e) {
          var vm = this
          if (window.FileReader) {
              var reader = new FileReader();
              reader.readAsText(e.target.files[0]);
              // Handle errors load
              reader.onload = function(event) {
              var csv = event.target.result;
              vm.parse_csv = vm.csvJSON(csv)
              // console.log(vm.parse_csv)
              };
              reader.onerror = function(evt) {
              if(evt.target.error.name == "NotReadableError") {
                  alert("Canno't read file !");
              }
              };
          } else {
              alert('FileReader are not supported in this browser.');
          }
        }
    }
}

</script>

<style>
.panel {
  border: 2px solid #dfdfdf;
  box-shadow: rgba(0, 0, 0, 0.15) 0 1px 0 0;
  margin: 10px;
} 
.panel.panel-sm {
  max-width: 100%;
  margin: 10px auto;
}
.panel-heading {
  border-bottom: 2px solid #dfdfdf;
}
.panel-heading h1, .panel-heading h2, .panel-heading h3, .panel-heading h4, .panel-heading h5, .panel-heading h6 {
  margin: 0;
  padding: 0;
}
.panel-body .checkbox-inline {
  padding: 15px 20px;
}
table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}
</style>
